import React from 'react';
import { Link } from 'react-router-dom';
// import "./Unauthorised.css";

const Unauthorised = () => {
    return (
        <>
            <h1 className='una-title'>401</h1>
            <h2 className='una-titleh2'>UNAUTHORIZED!</h2>
            <br />
            <h2 className='una-titleh2'><Link to={`/login`} className="una-link">CLICK HERE TO LOGIN</Link></h2>
        </>
    )
}

export default Unauthorised