import React from "react";
import "./Comments.css";
import { useContext, useState, useEffect } from "react";
import { Context } from "../../context/Context";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import axios from "axios";
import Singlecomment from "./singlecomment";

const Comments = ({ postid }) => {
  const { user } = useContext(Context);
  const [comment, setComment] = useState("Type your Comment Here...");
  const [comments, setComments] = useState([]);
  useEffect(() => {
    const getPost = async () => {
      const res = await axios.get("/api/posts/" + postid);
      setComments(res.data.comments);
    };
    getPost();
  }, [comments, postid]);

  const handlesubmit = async () => {
    if (comment === "" || comment === "Type your Comment Here...") {
      toast.error("Please Fill the Comment Box", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
    } else {
      try {
        // var postid = window.location.pathname.split("/")[2];
        await axios.put(`/api/posts/addcomment/${postid}`, {
          id: Date.now(),
          username: user.username,
          usercomment: comment,
        });
        toast.success("Comment Posted Successfully...");
        setComment("");

      } catch (err) {
        toast.error(err);
      }
    }
  };
  return (
    <div className="comments">
      <ToastContainer />
      <h1 className="comments-title">Comments</h1>
      <div className="comments-form">
        {user ? (
          <div className="createComment">
            <label htmlFor="comment">Your Comment</label>
            <textarea
              id="comment"
              type="text"
              placeholder={comment}
              value={comment}
              onChange={(e) => setComment(e.target.value)}
              required
            />
            <button
              type="submit"
              className="comment-submit"
              onClick={handlesubmit}
            >
              Post Comment
            </button>
          </div>
        ) : (
          <p>Login to Post your Comments</p>
        )}
      </div>
      <div className="comments-body">
        <div className="single-comment">
          {comments.map((com) => {
            return <Singlecomment com={com} />
          })}
        </div>
      </div>
    </div>
  );
};

export default Comments;
