import { React, useContext } from 'react';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';
import './Content.css';
import axios from 'axios';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { Context } from '../../context/Context';

const Content = ({ cuser, setUserchange }) => {
    const PF = "http://localhost:5000/images/";
    const { user } = useContext(Context);
    const navigate = useNavigate();
    const handledelete = async (id) => {
        confirmAlert({
            title: 'Confirm to Detele',
            message: 'Are you sure to delete user and all posts of this user',
            buttons: [
                {
                    label: 'Yes',
                    onClick: async () => {
                        try {
                            console.log(user);
                            await axios.delete(`/api/users/${id}`, {
                                data: { adminmode: true, user: user },
                            });
                            setUserchange(" ");
                            toast.success("User and Posts are deleted Successfully")
                            navigate('/users');
                        } catch (error) {
                            toast.error(error);
                        }
                    }
                },
                {
                    label: 'No',
                    onClick: () => { }
                }
            ]
        });
    }
    return (
        <>
            <div className="card">
                <div className="leftcontent">
                    <div className="profimg">
                        <img style={{
                            width: "100%", height:
                                "100%", borderRadius: "50%"
                        }} src={PF + cuser.profilePic} alt="Profile" />
                    </div>
                    <div className="profdetails">
                        <div className="username"> <strong>USERNAME</strong> : {cuser.username}</div>
                        <div className="email"><strong>EMAIL</strong> : {cuser.email}</div>
                        <div className="role"><strong>ROLE</strong> : {cuser.role}</div>
                    </div>
                </div>
                <div className="rightcontent">
                    <div className="delete-btn">
                        <i className="singlePostIcon far fa-trash-alt" onClick={() => handledelete(cuser._id)}
                        ></i>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Content