import React from "react";

const Singlecomment = (props) => {
    console.log(props.com);
    return (
        <div className="comment">
            <div className="commentBody">
                <div className="commentHeader">
                    <h3 className="commentAuthor">{props.com.username}</h3>
                    <span className="publishDate">{new Date(props.com.id).toDateString()}</span>
                </div>
                <span className="commentContent">{props.com.comment}</span>
            </div>
        </div>
    );
};

export default Singlecomment;
